body{margin:0;padding:0;background:#f8f8f8;font-size:14px;font-family:Microsoft YaHei;line-height:1.5;color:#333}#content{margin:20px auto;min-width:500px;width:50%;background-color:#fff;border-radius:3px;box-shadow:0 0 20px #ddd}#content h4{margin-top:0;margin-bottom:0}.user-info{box-sizing:border-box;width:100%;height:140px;padding:20px 0 5px;text-align:center;background-color:#528298;color:#fff}.user-info__avatar{border-radius:50%}.work-list{display:flex;justify-content:space-between;padding:40px 20px;height:500px}.work-list-wrap .add-work{display:flex;justify-content:space-around;width:100%;margin-top:20px}.work-list-wrap .add-work input{padding:10px;width:60%;border:1px solid #ddd;border-radius:3px}.work-list-wrap .add-work input[type=button]{width:150px;cursor:pointer}.work-list-wrap .done-list-wrap,.work-list-wrap .todo-list-wrap{padding:20px;width:40%;border:1px solid #f2f2f2;border-top:2px solid #528298;box-shadow:0 0 10px #f2f2f2}.work-list-wrap .done-list-wrap ol,.work-list-wrap .todo-list-wrap ol{overflow:auto;height:400px;padding-left:25px}.work-list-wrap .done-list-wrap li,.work-list-wrap .todo-list-wrap li{padding-top:10px;padding-bottom:10px;max-width:180px}.work-list-wrap .todo-list__done{margin-left:20px;padding:2px 10px;background-color:#fff;border:1px solid #ddd;border-radius:3px;color:#333!important;text-decoration:none!important}.work-list-wrap .todo-list__done:hover{background-color:#f8f8f8}